import BrowserWindow from 'sketch-module-web-view'
import {GradientMaker} from './gradient'

export default function () {
  
  //Global Setup
  const options = {
    backgroundColor: '#FFFFFF',
    identifier: 'coolhue.id',
    width: 215,
    height: 450,
    resizable: false,
    movable: true,
    alwaysOnTop: true,
    minimizable: true,
    maximizable: false,
    title: "CoolHue 2.0",
    vibrancy: "appearance-based",
  }

  const browserWindow = new BrowserWindow(options)
  browserWindow.isAlwaysOnTop()
  browserWindow.loadURL(require('./view.html'))
  browserWindow.webContents.on('nativeGradientApplier', function (fetchedData) {
    GradientMaker(fetchedData.firstColor,fetchedData.secondColor);
  })
}