import sketch from 'sketch'
import UI from 'sketch/ui'
var Style = require('sketch/dom').Style

export function GradientMaker(firstStop, secondStop) {
    var doc = sketch.getSelectedDocument()
    var selectedLayers = doc.selectedLayers
    if (selectedLayers.length != null && selectedLayers.length != 0) {
        selectedLayers.forEach(layer => {
            if (layer.type === "Shape" || layer.type === "ShapePath") {
                var layerFrame = layer.frame;
                layer.style.fills = [{
                    fillType: Style.FillType.Gradient,
                    gradient: {
                        gradientType: Style.GradientType.Linear,
                        from: {
                            x: -.01,
                            y: 0,
                        },
                        to: {
                            x: 1,
                            y: 1,
                        },
                        stops: [{
                                position: 0,
                                color: firstStop,
                            },
                            {
                                position: 1,
                                color: secondStop,
                            },
                        ],
                    },
                }, ]
            } else {
                UI.message(` CoolHue can apply gradients on Shape Layer(s) only`)
            }
        })
    } else {
        UI.message(` Select Shape Layer(s) to apply CoolHue Gradients`)
    }
}